    };
  },
  methods: {
    retrieveCustomers() {
      //  const params = {
      //   LAWD_CD: '사직동',
      //   DEAL_YMD: '201912',
      //   serviceKey: decodeURIComponent('dGfDa9%2BKMT7%2Fde3pabp8NyJqgrzY8roBjPsl6AuJjoAtfz9s92qTR%2FS8KU%2FGKpWzCz7EefDTc9kwUtEYc6O29Q%3D%3D'),
      // };
      //const aaa='http://openapi.molit.go.kr/OpenAPI_ToolInstallPackage/service/rest/RTMSOBJSvc/getRTMSDataSvcAptTradeDev?LAWD_CD=11110&DEAL_YMD=201912&serviceKey=dGfDa9%2BKMT7%2Fde3pabp8NyJqgrzY8roBjPsl6AuJjoAtfz9s92qTR%2FS8KU%2FGKpWzCz7EefDTc9kwUtEYc6O29Q%3D%3D';
      //  http
      //   .get('http://openapi.molit.go.kr/OpenAPI_ToolInstallPackage/service/rest/RTMSOBJSvc/getRTMSDataSvcAptTradeDev', {
      //     params,
      //   })
      //  http
      //   .get(aaa)
      //   .then(response => {alert(response.data)})
      //   .catch(() => {
      //     this.errored = true;
      //   })
      //   .finally(() => (this.loading = false));
      http
        .get("/findAllEmployees")
        .then(response => (this.info = response.data))
        .catch(() => {
          this.errored = true;
        })
        .finally(() => (this.loading = false));
    },
    refreshList() {
      this.retrieveCustomers();
    },
    show_detail: function(employeeid) {
      alert(employeeid + "상세보기");
      //아래를 수정했다.
      this.$router.push("/detailcustomer/" + employeeid);
    },
    delete_emp(employeeid) {
      if (employeeid == 1) {
        alert("사장님은 퇴사할 수 없습니다.");
        return;
      }
      alert(employeeid + "가 퇴사합니다.");
      http
        .post("/deleteEmployee/" + employeeid)
        .then(response => {
          if (response.data.state == "succ") {
            alert("퇴사처리를 하였습니다.");
            this.retrieveCustomers();
          } else {
            alert("퇴사처리를 하지 못했습니다.");
          }
        })
        .catch(() => {
          this.errored = true;
        })
        .finally(() => (this.loading = false));
    }
  },
  filters: {
    salarydecimal(value) {
      var a = parseInt(value);
      return a.toFixed(2);
    }
  },
  mounted() {
    this.retrieveCustomers();
  }
};
</script>

